<template>
<div class="resultBox">
  <h4 class="lz-title-h4">核保结论</h4>
  <div class="resultText">
    <div class="question" v-html="intell.results.resultInfo"></div>
    <div class="btn" v-if="intell.results.success == 'true'">
      <div class="lz-isIndex"  @click="resultInfoAjax(1)">
        继续投保 
      </div>
    </div>
    <div class="btn" v-if="intell.results.success == 'false'">
      <div class="lz-isIndex"  @click="resultInfoAjax(0)">
        核保不通过
      </div>
    </div>
  </div>
</div>
</template>
<script>
  import { mapMutations } from "vuex";
  import { mapGetters } from "vuex";
  import _ from "lodash";
  export default {
    name: "Result",
    data() {
      return {
        writeInformDatas:''
      };
    },
    created() {
      this.getInfo()
    },
    methods: {
      /**获取订单数据 */
      getInfo(){
        this.$http.getHttp({
            name: "getInfo",
            params: [{ name: "key", val: this.intell.int_quey.key }],
            header: 2
          },res => {
            this.writeInformDatas = JSON.parse(res.data).info;
            console.log(this.writeInformDatas)
        });
      },
      /**智能核保跳订单页 */
      resultInfoAjax(type){
        if (type == 0) {
            window.location.href = location.origin + "/jump.html?url="
            + encodeURIComponent(location.origin+"/market/premium-family.html?id="+this.intell.int_quey.code+"&key="+this.intell.int_quey.key);
        }else{
          //存储智能核保所需orderNo
            this.writeInformDatas.writeInform.fromvalue.intelligent = {
                paperId:this.intell.paperId,
                orderNo:this.intell.orderNo
            };
            var load = this.$popup.loading("正在请求数据...");
            this.$http.getHttp({
                name: "setInfo",
                params:{info:this.writeInformDatas} ,
            },data => {
                console.log(data)
                this.$popup.closeLay(load);
                var process = JSON.parse(sessionStorage.getItem("productProcess"));
                if (process.orderPage == 1) {
                window.location.href = location.origin + "/jump.html?url="
                + encodeURIComponent("./market/writeInform-moreinsure.html?key="+data.data+"&proCode=" + this.intell.int_quey.code + "")
                } else if (process.orderPage == 2) {
                window.location.href = location.origin + "/jump.html?url="
                + encodeURIComponent("./market/writeInform-3.html?key=" + data.data+"&proCode=" + this.intell.int_quey.code + "")
                }
            });
        }
      },
      //返回提示用户弹窗
       goBack(next){
        var vm = this;
        this.$popup.inquiry("是否重新选择疾病",['重新开始','取消'],()=>{
          next()
        },()=>{
          next(false)
        })
      }
    },
    components: {},
    computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        "intell"
      ])
    },
    mounted() {
    },
    //监听返回
    beforeRouteLeave (to, from, next) {
      if (to.name == 'Index2' && from.name == 'Result2') {                 
        this.goBack(next)
        return
      }
      next()
    }
  };
</script>

<style lang="scss">
.resultBox {
  min-height: 100%;
  background-color: #fff;
  font-size: 16px;
  .resultText {
    padding: 15px;
    line-height: 1.5;
    background-color: #fff;
    border-bottom: 1px solid #ccc;
    .question {
      padding-bottom: 20px;
    }
  }
}
</style>


